import React from 'react';
import { Component } from 'react';
import './App.css';
import EventList from './EventList';
import CitySearch from './CitySearch';
import { extractLocations, getEvents } from './api';
import 'bootstrap/dist/css/bootstrap.css';

import './nprogress.css';
import NumberOfEvents from './NumberOfEvents';
import { WarningAlert } from './Alert';

import {
  ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer
} from 'recharts';
import ChartPie from './ChartPie';

class App extends Component {
  state = {
    events: [],
    locations: [],
    number: 32,
    currentCity: 'all',
  };

  updateEvents = (location, numberOfEvents) => {
    getEvents().then((events) => {
      const locationEvents = (location === 'all') ?
        events.slice(0, numberOfEvents) :
        events.filter((event) => event.location === location);
      if (this.mounted) {
        this.setState({
          events: locationEvents.slice(0, numberOfEvents),
          currentLocation: location
        });
      }
    });
  }

  updateNumber(eventNumber) {
    this.setState({ numberOfEvents: eventNumber });
    const { currentCity } = this.state;
    this.updateEvents(currentCity, eventNumber);
  }

  getData = () => {
    const {locations, events} = this.state;
    const data = locations.map((location) => {
      const number = events.filter((event) => event.location === location).length
      const city = location.split(', ').shift()
      return {city, number};
    })
    return data;
  };

  async componentDidMount() {
    const { numberOfEvents } = this.state;
    this.mounted = true;
    getEvents().then((events) => {
      if (this.mounted) {
        this.setState({ 
          events: events.slice(0, numberOfEvents),
          locations: extractLocations(events)
        });
      }
    });
  }

  componentWillUnmount(){
    this.mounted = false;
  }

  render() {
    if(!navigator.onLine){
      return(
        <div className="App">
        <div className="AppSearch">
          <WarningAlert text="You are offline. New events could not be loaded"/>
          <h1>Meet App</h1>
          <NumberOfEvents updateNumber={(e) => this.updateNumber(e)} />
          <h4>Choose your nearest city</h4>
          <CitySearch locations={this.state.locations} updateEvents={this.updateEvents} numberOfEvents={this.state.numberOfEvents}/>
          <div className="chart">
          <br></br><h4>Events in each city</h4>
          <ResponsiveContainer height={400} >
            <ScatterChart
              margin={{
                top: 20, right: 20, bottom: 20, left: 20,
              }}
            >
              <CartesianGrid />
              <XAxis type="category" dataKey="city" name="City" />
              <YAxis type="number" dataKey="number" name="Number of Events" />
              <Tooltip cursor={{ strokeDasharray: '3 3' }} />
              <Scatter data={this.getData()} fill="#8884d8" />
            </ScatterChart>
          </ResponsiveContainer>
          </div>
        </div>
        <EventList events={this.state.events} />
      </div>
      )
    }
    return (
      <div className="App">
        <div className="AppSearch">
          <h1>Meet App</h1>
          <NumberOfEvents updateNumber={(e) => this.updateNumber(e)} />
          <h4>Choose your nearest city</h4>
          <CitySearch locations={this.state.locations} updateEvents={this.updateEvents} numberOfEvents={this.state.numberOfEvents}/>
          <div className="chart">
          <br></br><h4>Events in each city</h4>
          <div className="data-vis-wrapper">
            <ChartPie events={this.state.events} />
            <ResponsiveContainer height={400} >
              <ScatterChart
                margin={{
                  top: 20, right: 20, bottom: 20, left: 20,
                }}
              >
                <CartesianGrid />
                <XAxis type="category" dataKey="city" name="City" />
                <YAxis type="number" dataKey="number" name="Number of Events" />
                <Tooltip cursor={{ strokeDasharray: '3 3' }} />
                <Scatter data={this.getData()} fill="#8884d8" />
              </ScatterChart>
            </ResponsiveContainer>
          </div>
          </div>
        </div>
        <EventList events={this.state.events} />
      </div>
    );
  }
}

export default App;
